import * as fertilizationService from "@/service/fertilization";

const state = () => ({
    fertilization: null,
    fertilizationLines: []
});

const actions = {
    async getFertilization({ commit }, cycleId) {
        try {
            const data = await fertilizationService.getFertilizationByCycleId(
                cycleId
            );
            commit("SET_FERTILIZATION", data);
            return Promise.resolve(data);
        } catch (err) {
            return Promise.reject();
        }
    },
    async createFertlization({ state }, payload) {
        try {
            const data = await fertilizationService.createFertlization(payload);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async getFertilizationLines({ state }, fertId) {
        try {
            const data = await fertilizationService.getFertilizationLine(
                fertId
            );
            state.fertilizationLines = data;
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    },
    async createFertilizationLine({ state }, payload) {
        try {
            const data = await fertilizationService.createFertilizationLine(
                payload
            );
            console.log(data);
            return Promise.resolve();
        } catch (err) {
            return Promise.reject();
        }
    }
};

const mutations = {
    SET_FERTILIZATION(state, payload) {
        state.fertilization = payload;
    }
};
const getters = {};

export default {
    namespaced: true,
    state,
    getters,
    mutations,
    actions
};
